.popover-product-info {
    position: fixed;
    top: 0;
    right: 0;
    left: 50%;
    bottom: 0;
    background: rgb(255, 255, 255);
    padding: 5px;
    border-radius: 10px 0 0 10px;
    z-index: 10000;
    height: 100%;
    overflow-y: scroll;
}

.slideover {
    -webkit-animation: .2s SlideOver;
            animation: .2s SlideOver;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.popover-product-info .box {
    height: 100%;
    border: 1px solid gainsboro;
    border-radius: 10px 0 0 10px;
    padding: 30px;
}

@-webkit-keyframes SlideOver {
    0% {
        -webkit-box-shadow: 100px 0 20px 0 rgba(0, 0, 0, 1);
                box-shadow: 100px 0 20px 0 rgba(0, 0, 0, 1);
        -webkit-transform: translateX(1000px);
                transform: translateX(1000px);
    }
    100% {
        -webkit-box-shadow: -13px 0 20px 0 rgba(0, 0, 0, .2);
                box-shadow: -13px 0 20px 0 rgba(0, 0, 0, .2);
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

@keyframes SlideOver {
    0% {
        -webkit-box-shadow: 100px 0 20px 0 rgba(0, 0, 0, 1);
                box-shadow: 100px 0 20px 0 rgba(0, 0, 0, 1);
        -webkit-transform: translateX(1000px);
                transform: translateX(1000px);
    }
    100% {
        -webkit-box-shadow: -13px 0 20px 0 rgba(0, 0, 0, .2);
                box-shadow: -13px 0 20px 0 rgba(0, 0, 0, .2);
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}



